.app-loader {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  width: 100%;
}
.app-loader-icon {
  font-size: var(--maxSize);
  color: var(--textColor);
  -webkit-animation: app-loader-rotation .6s infinite linear; /* Safari 4+ */ /* Fx 5+ */ /* Opera 12+ */
  animation:         app-loader-rotation .6s infinite linear;
}

.app-loader-msg {
  font-family: Lighter;
  font-size: var(--minSize);
}

@-webkit-keyframes app-loader-rotation {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
@keyframes app-loader-rotation {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


