.app-article-wrapper{
  position: relative;
  padding: 90px 1rem 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  min-height: 78vh;
}

.app-article-full {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.app-article{
  position: relative;
  width: 100%;
  background: var(--bgDarkerColor)
}

.app-article-content,
.app-article-content-txt {
  position: relative;
  width: 100%;
  height: 100%;
  min-height: 300px;
  z-index: 2;
  margin-bottom: 1rem;
}

.app-article-content-txt {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0 1.5rem;
}

.app-article-content-txt > p {
  font-family: Lighter;
  font-size: var(--textSize);
  line-height: 1.5rem;
  text-align: justify;
}

.app-article-img {
  position: relative;
  width: 100%;
  height: 125px;
  margin: 0rem 0 0.8rem;
  overflow: hidden;
}

.app-article-icon {
  color: var(--textColor);
  position: absolute;
  top: 0.75rem;
  right: 0.75rem;
  -webkit-transition-property: color;
  -o-transition-property: color;
  transition-property: color;
  -webkit-transition-duration: .5s;
       -o-transition-duration: .5s;
          transition-duration: .5s;
  z-index: 9999;
  text-decoration: none;
}

.app-article-title {
  margin: 0;
  padding: 0 1rem 0.25rem;
}
.app-article-title > a {
  font-family: Regular;
  font-size: var(--titleSize);
  color: var(--textColor);
  text-decoration: none;
  line-height: 2.25rem;
  cursor: pointer;
}

.app-article-title > a:hover {
  color: var(--selectColor);
}

.app-article-txt {
  padding: 0 1rem 2rem;
  text-align: justify;
}

.app-article-tags {
  position: absolute;
  bottom: 0;
  width: 90%;
  bottom: 1rem;
  margin: 0 1rem;
  font-family: Lighter;
  font-size: var(--tinySize);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.app-article-nav {
  position: absolute;
  width: 95%;
  left: 2.5%;
  top: 45px;
  z-index: 999999;
}

.app-article-tag {
  -ms-flex: 1 1;
      flex: 1 1;
  color: var(--secondColor);
}

.app-article-tag > span {
  color: var(--textColor);
}

.app-article-tags > small {
  margin-top: 3px;
}

.app-article-figure {
  position: relative;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 350px;
  overflow: hidden;
}

.app-article-figure-img,
.app-article-figure-img-render  {
  position: absolute;
  width: 100%;
  height: auto;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  z-index: 1;
  opacity: 0;
  -webkit-transition-property: opacity;
  -o-transition-property: opacity;
  transition-property: opacity;
  -webkit-transition-duration:.3s;
       -o-transition-duration:.3s;
          transition-duration:.3s;
  -webkit-transition-timing-function: linear;
       -o-transition-timing-function: linear;
          transition-timing-function: linear;
}

.app-article-figure-img-render {
  opacity: 1
}

.app-article-full-title {
  width: 100%;
  z-index: 2;
  padding: 0 4rem;
  margin-bottom: 0.5rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  text-align: center;
  font-size: var(--titleSize);
  font-family: Bold;
  line-height: 2.5rem;

}

.app-article-full-subtitle {
  text-align: center;
  color: var(--selectColor);
  font-family: Lighter;
  font-size: var(--maxSize);
  margin-top: 0;
}

@media only screen 
  and (min-width: 1200px) {
    .app-article-wrapper {
      width: 90%;
      margin: 0 auto 0;
      -ms-flex-direction: row;
          flex-direction: row;
      -ms-flex-pack: center;
          justify-content: center;
    }
    .app-article{
      width: 30%;
    }
    .app-article-even,
    .app-article-odd {
      margin: 3px;
    }
    .app-article-content-txt {
      width: 800px;
    }
  }

@media only screen 
  and (max-width: 1199px) 
  and (min-width: 850px) {
    .app-article-wrapper {
      width: 97%;
      margin: 0 auto;
      -ms-flex-direction: row;
          flex-direction: row;
      -ms-flex-pack: center;
          justify-content: center;
    }
    .app-article{
      width: 30%;
    }
    .app-article-even,
    .app-article-odd {
      margin: 3px;
    }
    .app-article-content-txt {
      width: 800px;
    }

  }

  @media only screen 
  and (max-width: 849px) 
  and (min-width: 650px) {
    .app-article-wrapper {
      -ms-flex-direction: row;
          flex-direction: row;
    }
    .app-article{
      width: 49.5%;
    }
    .app-article-even{
      margin: 0.5% 0.5% 0.5% 0
    }
    .app-article-odd {
      margin: 0.5% 0 0.5% 0.5%;
    }

  }

@media only screen 
  and (max-width: 649px) {
    .app-article-wrapper {
      -ms-flex-direction: column;
          flex-direction: column;
      padding-top: 8rem;
    }
    .app-article{
      width: 100%;
    }
    .app-article-title > a {
      font-size: var(--titleMinSize);
    }

    .app-article-even,
    .app-article-odd {
      margin: 0.5rem 0;
    }

    .app-article-figure {
      position: relative;
      margin: 0;
      padding: 0;
      width: 100%;
      height: 300px;
    }
    
    .app-article-figure img {
      position: absolute;
      width: auto;
      height: 100%;
      top: 0;
      left: 50%;
      -webkit-transform: translateX(-50%);
          -ms-transform: translateX(-50%);
              transform: translateX(-50%);
    }

}


